<template>
<div class="qa">
   <div class="accordionWrap" v-if="qaData.length !== 0">
      <Accordion
         v-for="(item, index) in qaData"
         :key="index"
         :orderNumber="index + 1"
         :title="item.title"
         :content="item.content"
         :visible="index === 0"
      ></Accordion>
   </div>
</div>
</template>

<script>
import { mapState } from 'vuex';
import Accordion from '@/components/Accordion/index.vue';
export default {
   data: () => ({
      qaData: [
         {  
            title: '?',
            content: 'XMLJSONZIPKMLKMZCAPGRIB2 '
         },
         {  
            title: '?',
            content: 'API40'
         },
         {  
            title: '?',
            content: '1'
         },
         {  
            title: '?',
            content: 'Google Chrome Firefox Microsoft Edge'
         },
      ]
   }),
   metaInfo() {
      return { title: this.seo.title, meta: this.seo.meta };
   },
   computed: {
      ...mapState("meta", { seo: state => state.metaInfo.qa })
   },
   components: {
      Accordion
   }
};
</script>
